import Index from './components/index.vue'
import Touzhi from './components/touzhi.vue'
import Safety from './components/safety.vue'
import Guide from './components/guide.vue'
import Gbright2 from './components/gb-right2.vue'
import Gbright1 from './components/gb-right1.vue'
import Gbright3 from './components/gb-right3.vue'

export default{
  routes:[
    {path:'/index',component:Index},
    {path:'/touzhi',component:Touzhi},
    {path:'/safety',component:Safety},
    {path:'/guide',component:Guide,
      children:[
        {path:'/gb-right1',component:Gbright1},
        {path:'/gb-right2',component:Gbright2},
        {path:'/gb-right3',component:Gbright3},
        {path:'/',redirect:'/gb-right1'}
      ]
    },

    {path:'*',redirect:'/index'}
  ]
}





